import Browserizr from 'browserizr'

  // ios    0
  // ios  debice     
  //     40
  //    36
export const getVirtualKeyboardHeight = () => {
  const browser = Browserizr.detect();
  if (!browser.isIOS()) {
    return 0;
  }

  if (browser.isIPhone() 
    || browser.isIPhone4() 
    || browser.isIPhone5() 
    || browser.isIPhone678() 
    || browser.isIPhone678plus()) {
    return 36;
  }

  if (browser.isIPhoneX()) {
    return 40;
  }

  return 40;
}